<template>
  <el-form :model="form" ref="form" :rules="rules" label-position="left">
    <el-form-item label="ISBN" prop="isbn">
      <el-col :span="10">
        <el-autocomplete
          v-model="form.isbn"
          placeholder="书籍的ISBN号"
          @select="onSelect"
          :fetch-suggestions="(val, cb) => searchBook(val, 5, cb)"
        >
          <template slot-scope="{item}">
            <img :src="item.images.small.replace('\\', '')" height="100px">
            <div v-text="item.title" class="title"></div>
            <span v-text="item.isbn13 || item.isbn10" class="isbn"></span>
          </template>
        </el-autocomplete>
      </el-col>
    </el-form-item>
    <el-form-item label="书本现状">
      <el-col :span="6">
        <el-button
          @click="showTestDialog = true"
          v-text="deprecationRate>0? `Redo`: `Do the test`"
          class="testBtn"
        ></el-button>
        <span
          class="deprecation-box"
          v-if="deprecationRate > 0"
          v-text="`Points:${deprecationRate}`"
        ></span>
      </el-col>
    </el-form-item>
    <el-dialog :visible.sync="showTestDialog" title="评估书籍状态">
      <el-form :model="testForm" ref="testForm" label-position="top">
        <el-form-item
          v-for="q in testForm.questions"
          :key="q.id"
          :label="q.label"
          :rules="[{required: true, message: '请选择一个选项', trigger: 'blur'}]"
        >
          <el-select v-model="q.ans" placeholder="请选择">
            <el-option
              :label="choice.description"
              :key="choice.id"
              :value="choice.value * q.weight"
              v-for="choice in q.choices"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-button @click="showTestDialog=false">提交</el-button>
      </el-form>
    </el-dialog>
    <el-form-item label="请上传书籍的最新照片" prop="photoIds">
      <el-upload
        ref="upload"
        name="photos"
        :auto-upload="false"
        :action="uploadImgUrl"
        list-type="picture"
        :before-upload="onBeforeUpload"
        :on-remove="removePic"
        :on-success="onUploadSuccess"
      >
        <el-button slot="trigger">选取照片</el-button>
        <el-button @click="handleUpload">上传</el-button>
        <slot name="csrf_token">未开启CSRF保护</slot>
      </el-upload>
    </el-form-item>
    <el-form-item label="说几句" prop="description">
      <el-input type="textarea" rows="3" maxlength="50" v-model="form.description"></el-input>
    </el-form-item>
    <user-addresslist-select
      v-model="form.addressId"
      label="地址"
      :userAddressList="userInfo.address"
      formProp="addressId"
    ></user-addresslist-select>
    <el-form-item>
      <el-button @click="onSubmitForm" type="primary">提交</el-button>
    </el-form-item>
  </el-form>
</template>


<script>
import request from "superagent";
import jsonp from "superagent-jsonp";
import { getPlaceNameByCode } from "../../../src/components/utils.js";
import { uploadImgUrl as uploadUrl } from "../../../src/settings/constants.js";
import UserAddressSelect from "components/UserAddressSelect.vue";
const apiUrl = "https://api.douban.com/v2/book";
const searchBook = (keyword, count, cb) => {
  // console.log(`${apiUrl}/search?q=${keyword}&count=${count}`);
  request
    .get(`${apiUrl}/search?q=${keyword}&count=${count}`)
    .use(jsonp({ timeout: 1000 }))
    .end((err, res) => {
      if (!err) {
        cb && cb(res.body.books);
      }
    });
};
const truncatedStr = (str, length) =>{
  let rst = str.length>length? str.substr(length-3) + '...': str; 
  return rst;
}
export default {
  name: "postbook-form",
  components: { [UserAddressSelect.name]: UserAddressSelect },
  props: {
    uploadImgUrl: {
      type: String,
      default: "no post url"
    },
    submitFormUrl: {
      type: String,
      default: ""
    },
    redirectUrl: {
      type: String,
      default: ""
    },
    userInfoUrl: {
      type: String,
      required: true
    }
  },
  data() {
    let idCount = 0;
    const makePctChoice = (description, val) => {
      return {
        id: idCount++,
        description: description,
        value: val
      };
    };
    return {
      showTestDialog: false,
      userInfo: {},
      currentBookImgSrc: undefined,
      defaultUploadImgUrl: this.uploadImgUrl,
      photoIds: [],
      idCount: 0,
      form: {
        deprecation: undefined,
        isbn: "",
        description: "",
        addressId: null,
        photoIds: [],
        book: {
          name: "",
          author: "",
          img_src: "",
          douban_id: undefined,
          publisher: "",
          pub_date: ""
        }
      },
      testForm: {
        questions: [
          {
            id: idCount++,
            label: "书籍封面是否清晰整洁？",
            choices: [
              makePctChoice("整洁如新", 0.9),
              makePctChoice("稍有破损", 0.7),
              makePctChoice("较明显破损", 0.5)
            ],
            ans: undefined,
            weight: 0.3
          },
          {
            id: idCount++,
            label:
              "书本内是否存在部分内容缺失或内容不清晰等影响阅读体验的情况？",
            choices: [
              makePctChoice("非常少", 0.8),
              makePctChoice("一般", 0.6),
              makePctChoice("较多", 0.5),
              makePctChoice("不存在类似情况", 1)
            ],
            ans: undefined,
            weight: 0.7
          }
        ]
      },
      rules: {
        deprecation: [
          {
            type: "number",
            min: 1,
            max: 100,
            message: "请在做了书籍状态测试之后再提交"
          }
        ],
        isbn: [
          {
            validator: (rule, val, cb) => {
              if (!val) {
                cb(new Error("请输入ISBN号"));
              } else if (!/^(\d{11}|\d{13})$/.test(val)) {
                cb(new Error("请输入正确的ISBN号， 或输入关键字查询"));
              } else {
                cb();
              }
            },
            trigger: ["blur", "change"]
          }
        ],
        addressId: [
          { type: "number", required: true, message: "请选择一个所在地址" }
        ],
        photoIds: [
          { type: "array", required: true, message: "请在上传了图片后再提交" }
        ],
        description: [
          {
            required: true,
            message: "请用文字描述一下你的书籍状态",
            trigger: "blur"
          }
        ]
      }
    };
  },
  computed: {
    deprecationRate() {
      let score = 0;
      for (var q of this.testForm.questions) {
        score += q.ans;
      }
      // console.log(score);
      return (score * 100).toFixed(2);
    }
  },
  methods: {
    searchBook(kw, count, cb) {
      return kw && searchBook(kw, count || 5, cb);
    },
    onSelect(item) {
      this.form.isbn = item.isbn13 || item.isbn10;
      this.form.book.name = truncatedStr(item.title, 50);
      this.form.book.other_name = truncatedStr(item.origin_title, 50);
      this.form.book.author = item.author;
      this.form.book.douban_id = item.id;
      this.form.book.publisher = item.publisher;
      this.form.book.pub_date = item.pubdate;
      this.form.book.img_src = item.image.replace("\\", "");
    },
    removePic(file, fileList) {
      if (file.response && file.response.photoId) {
        this.photoIds = this.photoIds.filter(
          id => id !== file.response.photoId
        );
        console.log(file, fileList);
        console.log(this.photoIds);
      }
    },
    handleUpload() {
      this.$refs.upload.submit();
    },
    onUploadSuccess(resp, file, fileList) {
      this.photoIds.push(resp.photoId);
      console.log(this.form);
      console.log(this.photoIds);
    },
    onSubmitForm() {
      this.form.photoIds = this.photoIds;
      this.form.deprecation = this.deprecationRate;

      this.$refs["form"].validate(valid => {
        if (valid) {
          this.submitFormUrl &&
            request
              .post(this.submitFormUrl)
              .send(this.form)
              .then(resp => {
                this.$confirm(
                  "谢谢你和你的精灵，请耐心等待审核。是否继续添加新的精灵？",
                  "添加成功",
                  {
                    confirmButtonText: "继续添加",
                    cancelButtonText: "查看审核进度",
                    distinguishCancelAndClose: true
                  }
                )
                  .then(() => {
                    this.resetForm();
                  })
                  .catch(action => {
                    if (action === "cancel") {
                      document.location.href = this.redirectUrl;
                    }
                  });
              })
              .catch(err => {
                err.message && this.$message.error(err.message);
              });
        } else {
          this.$message.warning("数据验证失败");
        }
      });
    },
    resetForm() {
      this.$refs["upload"].clearFiles();
      this.photoIds = [];
      this.testForm.questions.forEach((q, i) => {
        q.ans = undefined;
      });
      this.form.isbn = "";
      this.form.description = "";
      this.form.photoIds = [];
    },
    onBeforeUpload(file) {
      console.log("triggered");
      let isImage = file.type.startsWith("image");
      let allowedSize = file.size / 1024 / 1024 <= 3;
      isImage || this.$message("不支持的文件格式");
      allowedSize || this.$message("文件大小超过3M");

      return isImage && allowedSize;
    }
  },
  created() {
    request
      .get(this.userInfoUrl)
      .then(resp => {
        this.userInfo = resp.body.data;
      })
      .catch(err => {
        this.$message && this.$message(err.message);
      });
  }
};
</script>

<style lang="scss">
.deprecation-box {
  margin-left: 10px;
}

.isbn {
  color: silver;
  font-size: 1em;
}
</style>
