<template>
  <el-row :gutter="picProfileGutter">
    <el-col :span="6" style="text-align:center">
      <el-upload
        name="avator"
        :action="updateAvatorUrl"
        :show-file-list="false"
        :before-upload="onBeforeUpload"
        :on-success="onUploadSuccess"
      >
        <el-tooltip content="点击上传图像" placement="top">
          <img :src="user.avator_url" alt :height="avatorHeight + 'px'">
        </el-tooltip>
      </el-upload>
    </el-col>
    <el-col :span="6">
      <el-form label-position="left" inline>
        <el-form-item label="昵称">
          <span v-text="user.nickname"></span>
        </el-form-item>
        <el-form-item label="用户名">
          <span v-text="user.username"></span>
        </el-form-item>
        <el-form-item label="邮箱">
          <span v-text="user.email"></span>
        </el-form-item>
        <el-form-item label="归属学校">
          <span v-text="user.school"></span>
        </el-form-item>
        <el-form-item label="已借书籍数量">
          <span v-text="user.borrowed_book_nums"></span>
        </el-form-item>
      </el-form>
    </el-col>
  </el-row>
</template>

<script>
import request from "superagent";

export default {
  name: "userprofile-view",
  props: {
    requestUrl: {
      type: String,
      default: ""
    },
    updateAvatorUrl: String,
    picProfileGutter: {
      type: Number,
      default: 2
    },
    avatorHeight: {
      type: Number,
      default: 200
    }
  },
  data() {
    return {
      user: {
        avator_url: undefined,
        nickname: undefined,
        username: undefined,
        email: undefined,
        school: undefined,
        borrowed_book_num: undefined
      }
    };
  },
  created() {
    request
      .get(this.$props.requestUrl)
      .then(res => {
        this.user = Object.assign({}, this.user, res.body.data);
      })
      .catch(err => {
        this.$message && this.$message(err.message);
      });
  },
  methods: {
    onUploadSuccess(res, file) {
      this.user.avator_url = res.data.avator_url;
    },
    onBeforeUpload(file) {
      const isImage = file.type.startsWith("image");
      const isAllowedSize = file.size / 1024 / 1024 < 2;

      isImage || this.$message.warning("请上传图片文件作为头像");
      isAllowedSize || this.$message.warning("图片大小不得超过2M");
      console.log(this.updateAvatorUrl);

      return isImage && isAllowedSize;
    }
  }
};
</script>
