<template>
      <el-form-item :label="label" :prop="formProp">
        <el-select 
        :value="addressId" 
        @change="$emit('change', $event)"
        style="width: 30em">
          <el-option
            v-for="addr in addrList"
            :key="addr.id"
            :label="addr.addr_val"
            :value="addr.id"
          ></el-option>
        </el-select>
      </el-form-item>
</template>
<script>
import { getPlaceNameByCode } from "./utils.js";
export default {
  name: "user-addresslist-select",
  model: {
    prop: "addressId",
    event: "change"
  },
  props: {
    addressId: {
      type: Number,
      required: true
    },
    userAddressList: {
      type: Array,
      default: []
    },
    label: String,
    formProp: String,
    span: {
      type: Number,
      default: 24
    }
  },
  computed: {
    addrList() {
      return this.userAddressList.map(addr => {
        return Object.assign({}, addr, { addr_val: this.getLabel(addr) });
      });
    }
  },
  methods: {
    getLabel(address) {
      return getPlaceNameByCode(address.code) + address.detail;
    }
  }
};
</script>
