<template>
  <el-form :model="addressForm" ref="addressForm" size="small">
    <el-form-item>
      <region-picker
        :regionCode="addressForm.code"
        @change="addressForm.code=$event"
        style="width:30em"
      ></region-picker>
    </el-form-item>
    <el-form-item>
      <el-input
        type="textarea"
        v-model="addressForm.detail"
        placeholder="详细地址信息"
        style="width:30em"
      ></el-input>
    </el-form-item>
    <el-form-item inline>
      <el-col :span="5">
        <el-input v-model="addressForm.label" placeholder="标签（可选）" style="width:100%"></el-input>
      </el-col>
      <el-col :span="2" :offset="1">
        <el-button type="primary" icon="el-icon-plus" @click="onSubmit" size="mini" style="width:100%">添加</el-button>
      </el-col>
    </el-form-item>
  </el-form>
</template>


<script>
export default {
  props: {
    addressObj: String,
    onSelect: Function
  },
  model: {
    prop: "addressObj",
    event: "change"
  },
  data() {
    return {
      addressForm: {
        code: null,
        detail: null,
        label: null
      }
    };
  },
  created() {
    this.addressForm = Object.assign({}, this.addressObj);
  },
  methods: {
    reset() {
      this.$refs["addressForm"].resetFields();
    },
    onSubmit() {
      this.$refs["addressForm"].validate(valid => {
        if (valid) {
          this.$emit("change", this.addressForm);
          this.onSelect();
        } else {
          return false;
        }
      });
    }
  }
};
</script>
