<template>
  <el-form ref="profileForm" v-model="userProfile" label-position="top">
    <el-form-item prop="nickname" label="昵称">
      <el-input v-model="userProfile.nickname" maxlength="25" style="width:25em"></el-input>
    </el-form-item>
    <el-form-item prop="school" label="学校">
      <el-input v-model="userProfile.school" style="width:25em"></el-input>
    </el-form-item>
    <user-address-select
      v-model="currentAddressId"
      :userAddressList="userProfile.address"
      label="地址簿"
      formProp="address"
      :span="20"
    ></user-address-select>
    <el-form-item label="当前修改地址" v-if="currentAddress">
      <region-picker
        :regionCode="currentAddress.code"
        @change="currentAddress.code=$event"
        style="width:30em"
      ></region-picker>
    </el-form-item>

    <el-form-item>
      <slot name="label">
        <el-button type="text" @click="isAppendAddr=!isAppendAddr">新建地址
          <i :class="`el-icon-arrow-${isAppendAddr?'down':'up'} el-icon--right`"></i>
        </el-button>
      </slot>
      <el-collapse-transition>
      <user-address-form-item
       v-show="isAppendAddr" 
      v-model="newAddress" 
      :onSelect="appendAddress"></user-address-form-item>
      </el-collapse-transition>
    </el-form-item>
  </el-form>
</template>

<script>
import request from "superagent";
import RegionPicker from "components/RegionPicker.vue";
import UserAddressSelect from "components/UserAddressSelect.vue";
import UserAddressFormItem from "components/UserAddressFormItem.vue";

export default {
  name: "update-user-profile-view",
  props: {
    requestUrl: {
      type: String,
      required: true
    }
  },
  data() {
    return {
      currentAddressId: null,
      newAddress: {
        id: Date.now(),
        code: null,
        detail: null,
        label: null
      },
      isAppendAddr: false,
      userProfile: {
        address: [],
        nickname: "",
        school: ""
      }
    };
  },
  created() {
    this.fetchInfo();
    console.log("created.");
  },
  computed: {
    currentAddress() {
      let addr = this.userProfile.address.find(
        item => item.id === this.currentAddressId
      );
      return addr;
    }
  },
  methods: {
    fetchInfo() {
      request.get(this.requestUrl).then(
        resp => {
          this.userProfile = Object.assign(
            {},
            this.userProfile,
            resp.body.data
          );
          this.currentAddressId =
            this.userProfile.address.length > 0
              ? this.userProfile.address[0].id
              : null;
        },
        err => {
          this.$message.warning(err.message);
        }
      );
    },
    appendAddress() {
      if (this.newAddress.code && this.newAddress.detail) {
        let address = Object.assign({}, this.newAddress);
        this.userProfile.address.push(address);
        console.log(address);
        this.newAddress.code = "";
        this.newAddress.detail = "";
        this.newAddress.label = "";
        this.currentAddressId = this.newAddress.id;
      }
    }
  },
  components: {
    UserAddressSelect,
    RegionPicker,
    UserAddressFormItem
  }
};
</script>
